@import 'reset';
@import 'mixin';
@import 'grid';
@import 'orders';
$costColor: #D0021B;
$revenueColor: #7ED321;
$incomeColor: #4A90E2;
$shoppingColor: #f5a623;
$bgColor: #f2f2f2;
$titleColor: #9b9b9b;
$addBtnColor: #757575;
$newProColor: #ebebeb;

body {
    font-size: 16px;
    font-family: "Helvetica","Microsoft JhengHei","PingFangTC-Regular";
    background-color: $bgColor;
    .container {
        max-width: 970px; }
    .wrap {
        width: 100%;
        margin: 0 auto;
        .navBar {
            background-color: #000;
            height: 70px;
            .container {
                .logo,.identity {
                    font-size: 24px;
                    color: #fff;
                    font-weight: bold; }
                .menu {
                    margin-left: 80px;
                    li {
                        a {
                            font-weight: bold;
                            color: #fff;
                            padding: 26px 20px;
                            padding-bottom: 22px;
                            text-decoration: none;
                            letter-spacing: 1.5px;
                            border-bottom: 4px solid transparent;
                            transition: .3s all;
                            &:hover {
                                border-bottom: 4px solid #fff; } }
                        .active {
                            border-bottom: 4px solid #fff; } } } } }
        .container {
            .info {
                margin-top: 30px;
                i {
                    color: black;
                    padding: 0 8px;
                    padding-top: 4px; }
                .overview {
                    font-size: 24px;
                    color: #000000;
                    font-weight: bold;
                    line-height: 37px; }
                .date {
                    font-size: 16px;
                    color: $titleColor;
                    span {
                        display: inline-block;
                        padding-top: 4px; }
                    .interval {
                        display: inline-block;
                        margin-left: 10px;
                        .btn {
                            background-color: transparent;
                            position: relative;
                            &:focus {
                                box-shadow: none; }
                            &::after {
                                position: absolute;
                                top: 16px;
                                right: -8px;
                                border-top: .4em solid; } }
                        .dropdown-menu {
                            box-shadow: 1px 2px 10px 0 $titleColor;
                            border-radius: 2px;
                            .dropdown-item {
                                padding: 8px 1.5rem;
                                text-align: center;
                                &:hover {
                                    color: white;
                                    background-color: black; } } } } } }
            .totalInfo {
                margin-top: 20px;
                .container {
                    padding: 0;
                    & > * {
                        width: 300px;
                        height: 138px;
                        box-shadow: 0 0 10px 5px #EBEBEB;
                        border-radius: 3px;
                        background-color: #fff;
                        text-align: center; }
                    .title {
                        margin-top: 30px;
                        font-size: 16px;
                        color: #000000;
                        display: inline-block;
                        font-weight: bold; }
                    .number {
                        font-size: 36px;
                        margin-top: 16px;
                        font-weight: bold;
                        display: block; }
                    .green {
                        color: $revenueColor; }
                    .red {
                        color: $costColor; }
                    .blue {
                        color: $incomeColor; }
                    i {
                        font-size: 25px;
                        margin-right: 8px; } } }
            .chart {
                margin-top: 18px;
                height: 450px;
                width: 940px;
                background: #FFFFFF;
                box-shadow: 0 0 10px 5px #EBEBEB;
                border-radius: 3px;
                h2 {
                    font-weight: bold;
                    font-size: 24px;
                    padding: 30px 0 0 40px; }
                canvas {
                    padding-bottom: 30px; } }
            .transactionInfo {
                margin-top: 20px;
                margin-bottom: 42px;
                .container {
                    padding: 0;
                    & > * {
                        background: #FFFFFF;
                        box-shadow: 0 0 10px 5px #EBEBEB;
                        border-radius: 3px;
                        width: 460px;
                        height: 456px; }
                    .website {
                        font-size: 24px;
                        font-weight: bold;
                        padding: 30px 35px 0 35px;
                        .websiteTable {
                            margin-top: 10px;
                            li {
                                height: 92px;
                                border-bottom: 1px solid $newProColor;
                                &:last-child {
                                    border-bottom: none; }
                                .titleIcon {
                                    font-size: 52px;
                                    width: 52px;
                                    height: 52px; }
                                .url {
                                    font-size: 16px;
                                    color: $titleColor;
                                    padding: 0px 62px 0 20px;
                                    width: 200px; }
                                .number {
                                    font-size: 20px;
                                    font-weight: bold;
                                    margin-right: 20px;
                                    width: 62px;
                                    text-align: right; }
                                .upAndDowns {
                                    font-size: 16px;
                                    i {
                                        font-size: 18px;
                                        padding-right: 8px;
                                        height: auto;
                                        width: auto; } }
                                .up {
                                    color: $revenueColor; }
                                .down {
                                    color: $costColor; } } } }
                    .orders {
                        padding: 30px 35px 0 35px;
                        .title {
                            font-size: 24px;
                            font-weight: bolder; }
                        .orderList {
                            margin-top: 30px;
                            li {
                                height: 115px;
                                width: 100%;
                                position: relative;
                                border-bottom: 1px solid $newProColor;
                                padding: 8px 0px;
                                &:last-child {
                                    border-bottom: none; }
                                img {
                                    width: 100px;
                                    height: 100px; }
                                .orderInfo {
                                    margin-left: 20px;
                                    h2 {
                                        font-size: 20px;
                                        font-weight: bolder; }
                                    .orderTime,.orderPeople {
                                        font-size: 16px;
                                        color: $titleColor;
                                        i {
                                            margin-right: 16px;
                                            width: 10px;
                                            height: 10px; } }
                                    .orderTime {
                                        padding: 8px 0; }
                                    .orderPeople {
                                        i {
                                            font-size: 15px;
                                            padding-left: 5px; } }
                                    .totalMoney {
                                        position: absolute;
                                        right: 0px;
                                        bottom: 20px;
                                        span {
                                            font-size: 14px;
                                            display: block;
                                            text-align: right;
                                            &:last-child {
                                                font-size: 20px;
                                                font-weight: bolder;
                                                margin-top: 5px; } } } } } } } } } } } }
