$costColor: #D0021B;
$revenueColor: #7ED321;
$incomeColor: #4A90E2;
$shoppingColor: #f5a623;
$bgColor: #f2f2f2;
$titleColor: #9b9b9b;
$addBtnColor: #757575;
$newProColor: #ebebeb;

.checkAndEdit {
    input {
        margin: 32px 0 0 20px; }
    .editMenu {
        .btn {
            background-color: transparent;
            position: relative;
            font-weight: bold;
            font-size: 14px;
            margin-top: 18px;
            &:focus {
                box-shadow: none; }
            &::after {
                position: absolute;
                top: 13px;
                right: -5px;
                border-top: .4em solid; } }
        .dropdown-menu {
            min-width: 150px;
            .dropdown-item {
                position: relative;
                padding: .25rem 2rem;
                input {
                    position: absolute;
                    top: 5px;
                    left: 13px; } } } } }
.tableSetting {
    table {
        width: 100%;
        tr:nth-child(even) {
            background-color: $newProColor; }
        th {
            padding: 12px 0;
            font-weight: bold;
            font-size: 16px;
            background-color: #fff;
            padding-left: 40px; }
        td {
            font-size: 14px;
            line-height: 21px;
            padding-left: 40px;
            padding-top: 17px;
            position: relative;
            input {
                position: absolute;
                left: 20px;
                top: 27px; }
            .price {
                color: $titleColor; }
            .statusBtn {
                font-size: 14px;
                font-weight: bold;
                color: #fff;
                i {
                    padding-left: 5px; } }
            .green {
                background-color: $revenueColor; }
            .orange {
                background-color: $shoppingColor; }
            .blue {
                background-color: $incomeColor; }
            .gray {
                background-color: $titleColor; }
            .productList {
                &:last-child {
                    margin-bottom: 17px; } } } } }
.pageBtn {
    width: 240px;
    height: 40px;
    border-radius: 4px;
    margin: 30px 0 40px 0;
    li {
        a {
            display: block;
            width: 41px;
            height: 41px;
            line-height: 41px;
            text-align: center;
            color: black;
            border: 1px solid #ECEEEF;
            background-color: white;
            text-decoration: none; }
        .active {
            background-color: black;
            color: white; } } }
